import React from 'react'
// import PropTypes from 'prop-types'
import { connect } from 'dva'
import { routerRedux } from 'dva/router'
import { Row, Col, Button } from 'antd'
// import ExportJsonExcel from 'js-export-excel'
// import queryString from 'query-string'
// import Model from './Model'
// import Detail from './Detail'

import style from './index.less'
// import style from './songlists.less'
// const { Search } = Input
interface Iprops {
  ycyh: any,
  dispatch: any,
}
const Index = ({ ...props }: Iprops) => {
  console.log(props)
  const {
    ycyh,
    dispatch,
  } = props
  const {
    regprofile,
  } = ycyh
  const openrRegprofile = () => {
    dispatch(routerRedux.push({
      pathname: '/signup/detail',
    }))
  }
  return (
    <Row type="flex" gutter={32} style={{ textAlign: 'center' }}>
      <Col span={12}>
        <div className={style.col}>
          <div>报名点概况</div>
          <Row className={style.mgb10}>
            <Col span={12}>
              广东赛区：49
            </Col>
            <Col span={12}>
              XX赛区：49
            </Col>
          </Row>
          <Row className={style.mgb10}>
            <Col span={12}>
              广东赛区：49
            </Col>
            <Col span={12}>
              XX赛区：49
            </Col>
          </Row>
          <Row className={style.mgb10}>
            <Col span={12}>
              广东赛区：49
            </Col>
            <Col span={12}>
              XX赛区：49
            </Col>
          </Row>
          <Row className={style.mgb10}>
            <Col span={12}>
              广东赛区：49
            </Col>
            <Col span={12}>
              XX赛区：49
            </Col>
          </Row>
          <Button>查看</Button>
        </div>
      </Col>
      <Col span={12}>
        <div className={style.col}>
          <div>报名概况（总计：{regprofile.sum}）</div>
          <Row className={style.mgb10}>
            <Col span={12}>
              广东赛区：{regprofile[0]}
            </Col>
            <Col span={12}>
              XX赛区：49
            </Col>
          </Row>
          <Row className={style.mgb10}>
            <Col span={12}>
              广东赛区：{regprofile[1]}
            </Col>
            <Col span={12}>
              XX赛区：{regprofile[2]}
            </Col>
          </Row>
          <Row className={style.mgb10}>
            <Col span={12}>
              广东赛区：{regprofile[3]}
            </Col>
            {/* <Col span={12}>
              XX赛区：49
            </Col> */}
          </Row>
          {/* <Row className={style.mgb10}>
            <Col span={12}>
              广东赛区：49
            </Col>
            <Col span={12}>
              XX赛区：49
            </Col>
          </Row> */}
          <Button onClick={openrRegprofile}>查看</Button>
        </div>
      </Col>
    </Row>
  )
}
export default connect(({
  ycyh, dispatch,
}: Iprops) => ({
  ycyh, dispatch,
}))(Index)



